.TodoSearch {
  background: #F9FBFC;
  border-radius: 8px;
  border: 2px solid #202329;
  margin: 0 24px;
  height: 64px;
  width: calc(100% - 62px);
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  opacity: 0.8;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

body.timed-night .TodoSearch,
body.timed-afternoon .TodoSearch {
  color: white;
  background: #1E1E1F;
}

.TodoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

body.timed-night .TodoSearch::placeholder {
  color: #F9FBFC;
}

.TodoSearch:focus {
  outline-color: #F7CDCA;
}