form {
  width: 90%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #a99d97;
  padding: 33px 40px;
  border-radius: 8px;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 26px;
}

textarea {
  background-color: #f9fbfc;
  border: 2px solid #202329;
  border-radius: 8px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1e1e1f;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100% - 25px);
}

textarea::placeholder {
  color: #a99d97;
  font-family: 'Montserrat';
  font-weight: 400;
}

textarea:focus {
  outline-color: #f7cdca;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.TodoForm-button {
  width: 120px;
  height: 48px;
  font-size: 20px;
  font-family: 'Montserrat';
  border-radius: 4px;
  border: none;
  background: transparent;
  cursor: pointer;
  transition: background-color 0.4s ease, box-shadow 0.4s ease, color 0.4s ease;
}

.TodoForm-button--hide {
  cursor: default;
  color: transparent;
}

.TodoForm-button--add {
  background-color: #f7cdca;
  box-shadow: 0px 5px 25px rgba(247, 205, 202, 0.5);
}
