.ModalBackground {
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.overlay__container {
  display: flex;
  align-items: center;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  position: absolute;
}

.overlay__img {
  width: 250px;
  border-radius: 8px;
}

.overlay__message {
  padding: 20px;
  text-align: center;
}

.overlay__description {
  margin: 4px;
}

.overlay__button {
  width: 50px;
  height: 30px;
  position: inherit;
  bottom: 10px;
  right: 20px;
  background-color: #849ae3;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 10px 20px 20px 10px;
}

.overlay__button:hover {
  background-color: #FFC0CB;
  border: 2px solid rgba(127, 0, 255, 0.5);
}